.block-a11y-checks-settings {
  overflow: hidden;
  background-color: white;
  margin: 30px 30px 30px 10px;
  border-radius: 10px;
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.05);
  color: #1f1f1f;
}
.block-a11y-checks-settings h1 {
  color: #ffffff;
  background: #2271b1;
  padding: 25px 23px;
  margin: 0;
}
.block-a11y-checks-settings table tr {
  border-bottom: 1px solid #dedede;
}

.block-a11y-checks-settings-form {
  display: grid;
  grid-template-columns: 3fr 2fr;
  gap: 50px;
  margin: 0 25px;
}

.block-a11y-checks-settings-info {
  border: 1px solid #dedede;
  border-radius: 10px;
  padding: 25px;
  margin: 25px 0;
}
.block-a11y-checks-settings-info h2 {
  font-size: 1.6em;
  margin: 0;
}
.block-a11y-checks-settings-info p {
  font-size: 1.2em;
}

.block-check-radio-options {
  display: flex;
  gap: 25px;
  align-items: center;
}